html {
  font-size: 125%;
  margin: 0;
  top: 0;
}

.App {
  background: linear-gradient(#283E51, #4B79A1, #C4E0E5);
  text-align: center;
  height: 100vh;
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
}

.wrapperSmall {
  width: 80%;
  margin: 0 auto;
}

.wishWrapper {
  overflow: hidden;
}


/* General Text styles */
p {
  padding: 0;
  margin: 0;
  font-family: 'Amatic SC', cursive;
  font-weight: bold;
  color: #16222d;
}

h1 {
  font-family: 'Shrikhand', cursive;
  font-size: 1.7rem;
  color: #EBEAE9;
  line-height: 1;
  padding: 0;
  margin: 10px 0;
}

header p {
  color: #EBEAE9;
  font-size: 1.3rem;
  padding: 0;
}

form input::placeholder,
form button, 
form input[type="text"] {
  font-size: 1.3rem;
  text-align: center;
  font-family: 'Amatic SC', cursive;
  font-weight: bold;
}

/* Style Header */

header {
  position: relative;
  top: 25vh;
}

/* form styles */

form {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  padding: 20px 0;
}

form button,
form input[type="text"] {
  margin: 5px 0;
  padding: 10px 0;
  border-radius: 25px;
  border: 2px solid #9b3943;
  color: #16222d;
  transition: background 0.5s, transform 0.5s, box-shadow 0.5s;
}

form button {
   background: rgba(223, 125, 136, 1);
}

form input[type="text"] {
   background: rgba(223, 125, 136, 0.7);
}

form button {
  box-shadow: 5px 5px #9b3943;
}

form button:focus,
form input[type="text"]:focus {
  box-shadow: 0 0 15px #9b3943;
}

form input[type="text"]:active,
button:active{
  background: #9b3943;
}

button:active {
    transform: translate(4px, 4px);
    box-shadow: 1px 1px  #9b3943; 
}

/* Style dynamic wishes */

.animationTime {
  position: absolute;
  font-size: 0.3rem;
  border-radius: 50%;
  transition: transform 0.7s, top 0.7s, left 0.7s;
}

.balloon { 
  border: 1px solid #9b3943;
  background: rgba(223, 125, 136, 0.7);
  border-radius: 50%;
  height: 100px;
  width: 75px;
  text-align: center;
  padding: 20px;
  box-shadow: 1px 1px 5px #9b3943;
  position: relative;
}

.balloon::after {
 content: '';
 position: absolute; 
 height: 50%;
 width: 20px;
 border: 1px solid #9b3943;
 top: 100%;
 left: 60%;
 border-radius: 50%;
transform: translate(-20px, 0) scale(2,1);
border-bottom: none;
border-top: none;
border-right: none;

}

.balloon::before{
 content: '';
 position: absolute; 
 height: 50%;
 width: 20px;
 border: 1px solid #9b3943;
 top: 144%;
 left: 44%;
 border-radius: 50%;
transform: translate(-20px, 0) scale(2,1);
border-bottom: none;
border-top: none;
border-left: none;

}

/* style wishes in a seeminly random balloon form at top of page */
.animationTime:nth-of-type(even) {
  top: 10%;
  left: 5%;
  z-index: 3;
  transform: rotate(10deg);
}
.animationTime:nth-of-type(odd) {
  top: 5%;
  left: 7%;
  z-index: 4;
  transform: rotate(-10deg);
}
.animationTime:nth-child(3n) {
  top: 7%;
  left: 65%;
  z-index: 3;
  transform: rotate(-5deg);
}
.animationTime:nth-child(4n) {
  top:6%;
  left: 25%;
  transform: rotate(5deg);
}
.animationTime:nth-child(5n) {
  top: 8%;
  left: 40%;
  z-index: 4;
  transform: rotate(15deg);
}
.animationTime:nth-child(6n) {
  top: 10%;
  left: 69%;
  transform: rotate(-15deg);
}
.animationTime:nth-child(7n) {
  top: 9%;
  left: 40%;
  transform: rotate(20deg);
}
.animationTime:nth-child(9n){
  top: 5%;
  left: 45%;
  transform: rotate(20deg);
}

/* alert */

.swal2-popup {
  font-family: 'Amatic SC', cursive;
  background: #f1bf98;
  color: #283E51;
}

.swal2-confirm.swal2-styled {
  font-family: 'Raleway', sans-serif;
  background: #283E51;
  color: #EBEAE9;
}

