/* style last of type and hover and animation at the end so other styles don't override it */
.animationTime:last-of-type {
  top: 10%;
  left: 45%;
  display: block;
  margin: 0 auto;
  z-index: 8;
  transform: rotate(0);
  font-size: 0.7rem;
}

.animationTime:hover {
  z-index: 9;
  transform: scale(3);
  top: 25%;
  font-size: 1rem;
  padding: 0;
}

.balloon:hover {
  padding: 15px 5px 5px;
}

.animationTime.lastWish { 
  animation: moveUpPage 15s 1 linear;
}

.animationTime.lastWish .balloon {
  animation: wobble 1s alternate 14 ease-in-out;
}

@keyframes moveUpPage {
  from{top: 100%}
  to{
    top: 10%;
    left: 45%;
  }
}
@keyframes wobble {
  to{transform: translateX(50px)}
}